	module.exports = {"data":{"post":{"html":"<p>To bad we didn't do the weblog shootout yesterday... we would have had a lot of fun pictures. We're having the patio extended behind the carport, and a mini-backhoe rumbled through our backyard to haul away the dirt. We also had bath day with Christopher, attended a baby-flea-market in Holm-Seppensen (we spent DM 10 for a stocking cap, a bottle warmer, and a Fisher-Price radio), we had a very pregnant visitor, and I started reorganizing my desk, moving the secondary PCs to the shelves behind me. Today all we have planned is swimming at the Salztherme (salt spa) in Lüneburg.</p>","fields":{"title":"Olympic Shootout","date":"17 Sep 2000","url":"https://papascott.de/2000-09-17-olympic-shootout/"}}},"pathContext":{"slug":"/2000-09-17-olympic-shootout/","prev":{"fields":{"title":"Busy busy","date":"12 Sep 2000","slug":"/2000-09-12-busy-busy/"}},"next":{"fields":{"title":"Relief!","date":"23 Sep 2000","slug":"/2000-09-23-relief/"}}}}

